import React from 'react';
import '../css/home.css';
import styled from "styled-components";
import { useEffect, useState, useRef, useCallback, useContext } from 'react';
// import '../css/question.css'
import '../css/mainfunc.css'
import Webcam from 'react-webcam';
import axios from 'axios';
import dayjs from 'dayjs';
import mimeType  from 'mime';
import { useNavigate } from 'react-router-dom';

const AllWrapper = styled.div`
  display:block;
  flex-direction: column;
  margin:auto;
  height:auto;
  width:700px;
  text-align: center;
  align-items:center;
  margin-top:-30px;
}

`;
const VideoRecorder = () => {
  const contentRef = useRef();
  const videoRef = useRef(null);
  const [content, setContent] = useState("");
  const mediaRecorder = useRef(null);
  const videoChunks = useRef([]);
  const navigate = useNavigate();
  const [recommendId, setRecommendId]=useState();
  const [loading, setLoading] = useState(false); //    
  //  
  const [text,setText] = useState("");

  const textHandler = (e) => {
    e.preventDefault();
    setText(e.target.value);
  };


//   // 
  const getMediaPermission = useCallback(async () => {
    try {
      console.log("video start");
      const audioConstraints = { audio: true };
      const videoConstraints = {
        audio: false,
        video: true,
      };

      const audioStream = await navigator.mediaDevices.getUserMedia(
          audioConstraints
      );
      const videoStream = await navigator.mediaDevices.getUserMedia(
          videoConstraints
      );

      if (videoRef.current) {
          videoRef.current.srcObject = videoStream;
      }

      // MediaRecorder 
      const combinedStream = new MediaStream([
          ...videoStream.getVideoTracks(),
        ...audioStream.getAudioTracks(),
      ]);

      const recorder = new MediaRecorder(combinedStream, {
          mimeType: 'video/webm',
      });

      recorder.ondataavailable = (e) => {
        if (typeof e.data === 'undefined') return;
        if (e.data.size === 0) return;
        videoChunks.current.push(e.data);
        console.log("video stop")
      };

      mediaRecorder.current = recorder;
    } catch (err) {
      console.log(err);
    }
  }, []);

  const downloadVideo = () => {
    const videoBlob = new Blob(videoChunks.current, { type: mimeType });
    const videoUrl = URL.createObjectURL(videoBlob);
    const link = document.createElement('a');
    link.download = `My video - ${dayjs().format('YYYYMMDD')}.webm`;
    link.href = videoUrl;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };


  const sendHandler=(e)=>{
    e.preventDefault();
    const videoBlob = new Blob(videoChunks.current, { type: 'video/webm' });
    const formData = new FormData();
    formData.append("text",text)
    formData.append("video", videoBlob); 
    formData.append("access_token", window.localStorage.getItem('token')); 
    setLoading(true); //       true 

    fetch('http://ec2-13-124-237-120.ap-northeast-2.compute.amazonaws.com:8000/main/recommendation/',{
      method:'POST',
      hearders:{
        // 'Content-Type': 'multipart/form-data; application/json; charset=utf-8'
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body:formData,
    
    })
    .then((res) => res.json())
    .then((res) => {
      console.log(res);
      console.log('');
      setRecommendId(res.data.recommend_info.recommend_id);
      console.log(res.data.recommend_info.recommend_id);
      window.localStorage.setItem('recommend_id', res.data.recommend_info.recommend_id);
      handleRecommendBook();
    })
    .catch((error) => {
      console.error('Error sending data:', error);
    })
    .finally(() => {
      setLoading(false); //       false 
    });
  }

  const handleRecommendBook=()=>{
    navigate('/recommend',{
      state:{
        recommend_id:`${recommendId}`
      },
    });
  };
  useEffect(() => {
    getMediaPermission();

  }, []);

  return (
    
    <AllWrapper>
      <div className='question'>
        <p className='explane'> <strong></strong>     <br/>    . <br/> <strong> </strong>    ,<br/>        .</p>

        <p>   ?</p>
      </div>
      <form>
        <div>
          
          <section>
          {/* <video ref={videoRef} autoPlay /> */}
            {/* <Webcam
              width={'300px'}
              border-radius={'10px'}/> */}
            <textarea
              placeholder="   . ( 300)"
              type="text"
              name="diary"
              onChange={textHandler} 
              maxLength={300}/>

          </section>
        </div>
        <div className='submitdiv'>
          <button class="submit" type='submit' onClick={sendHandler}>{loading ? ' ...' : 'Submit'}</button>
        </div>
      </form>

    </AllWrapper>
  );
}
  
    
    
  
  export default VideoRecorder;